<template>
    <loading useResourceManager />
    <TresCanvas v-bind="state">
        <TresPerspectiveCamera :position="[15, 15, 15]" :fov="45" :near="0.1" :far="1000" />
        <OrbitControls v-bind="controlsState" />

        <lights />
        <rooms v-if="Resource.hasAllFinished.value" />

        <Suspense>
            <tv />
        </Suspense>

        <reflectorDUDV v-bind="reflectorState" />

        <Suspense>
            <env />
        </Suspense>
</TresCanvas>
</template>

<script setup lang="ts">
import { SRGBColorSpace, BasicShadowMap, NoToneMapping } from 'three'
import { reactive } from 'vue'
import { TresCanvas } from '@tresjs/core'
import { OrbitControls } from '@tresjs/cientos'
import { reflectorDUDV } from 'PLS/floor'
import { yangyangLoading as loading } from 'PLS/UIdemo'
import { Resource } from 'PLS/resourceManager'
import rooms from '../components/rooms.vue'
import lights from '../components/lights.vue'
import env from '../components/env.vue'
import tv from '../components/tv.vue'

const state = reactive({
    clearColor: '#000000',
    shadows: false,
    alpha: false,
    shadowMapType: BasicShadowMap,
    outputColorSpace: SRGBColorSpace,
    toneMapping: NoToneMapping,
    windowSize: true
})

const reflectorState = {
    reflectivity: 8,
    showGridHelper: false,
    scale: 6.6,
    mapUrl: './plugins/freeDigitalHome/image/waterdudv.jpg',
}

const controlsState = reactive({
    enableDamping: true,
    dampingFactor: 0.05,
    enableZoom: true,
    // autoRotate: true,
    autoRotateSpeed: 2,
})

Resource.loadResources([{ functionName: 'GLTFLoader', url: './plugins/freeDigitalHome/models/floor_plan.glb', resourceID: 'rooms' }])
</script>
