<template>
    <TresMesh :visible="!tvon.switch" :position="[0.58, 1.526, 4.67]" :rotation="[0, -Math.PI / 2, 0]" :renderOrder="9999">
        <TresPlaneGeometry :args="[1.36, 0.77]" />
        <TresMeshBasicMaterial :map="texture" />
    </TresMesh>
    <Html v-bind="state" :position="[0.58, 1.526, 4.67]">
        <div class="cStyle1 pos-relative text-white"></div>
        <div class="parallelogram tv" :class="{ off: tvon.switch }">
            <span @click="clicktv">üì∫ ÁîµËßÜ</span>
            <div class="contentDiv">ÁÇπÂáªÂºÄÂÖ≥</div>
        </div>
    </Html>
</template>
<script lang="ts" setup>
import { ref } from 'vue'
import * as THREE from 'three'
import { useLoop } from '@tresjs/core'
import { useTexture } from 'PLS/basic'
import { useVideoTexture, Html } from '@tresjs/cientos'
import { useHaswStore } from 'PLS/freeDigitalHome/stores/hasw'

const haswStore = useHaswStore() as any

const tvon = haswStore.tv
const updateTexture = (pTexture: any) => {
    useTexture(tvon.live).then((newTexture) => {
        pTexture.value.image = newTexture.image
        pTexture.needsUpdate = true
    })
}

const texture = ref(null) as any
if (tvon.live) {
    const textureLoader = new THREE.TextureLoader()
    textureLoader.setCrossOrigin('anonymous')
    texture.value = textureLoader.load(
        tvon.live,
        () => {
            console.log('Texture loaded')
        },
        undefined,
        async (err) => {
            console.error('Error loading texture:', err)
            tvon.live = ''
            texture.value = await useVideoTexture('./plugins/freeDigitalHome/video/xiaomi.mp4', { loop: true })
        },
    )
    // texture.value = await useTexture([tvon.live])
} else {
    texture.value = await useVideoTexture('./plugins/freeDigitalHome/video/xiaomi.mp4', { loop: true })
}
texture.value.colorSpace = THREE.SRGBColorSpace

const clicktv = () => {
    tvon.switch = !tvon.switch
}

const state = {
    wrapperClass: 'illustrate2',
    as: 'div',
    sprite: true,
    transform: true,
    distanceFactor: 3,
    center: true,
}

const { onBeforeRender } = useLoop()
let lastTime = 0
onBeforeRender(({ elapsed }) => {
    const cu = Math.floor(elapsed * 10)
    if (cu === lastTime) {
        return
    }
    if (cu % 10 === 0) {
        lastTime = cu
        if (tvon.live) {
            updateTexture(texture)
        }
    }
})
</script>
<style lang="less">
.illustrate2 {
    .parallelogram.tv::before {
        background: linear-gradient(45deg, #6bfff5, #1a0afa);
    }
    .parallelogram.off::before {
        background: linear-gradient(45deg, #000000, #ffffff);
    }
}
</style>
