<template>
    <TresGroup>
        <primitive :object="scene" :scale="1.0" :position="[-8.832, -2.036, 2.174]" :rotation="[0, 0, 0]" />
        <livingRoomInfo :temperature="haswStore.infors.living.temp" :humidity="haswStore.infors.living.hum"/>
        <bedRoomA />
        <bedRoomB />
    </TresGroup>
</template>

<script setup lang="ts">
import * as THREE from 'three'
import { Resource } from 'PLS/resourceManager'
import livingRoomInfo from './text/livingRoomInfo.vue'
import bedRoomA from './text/bedRoomA.vue'
import bedRoomB from './text/bedRoomB.vue'
import { useHaswStore } from 'PLS/freeDigitalHome/stores/hasw'

const haswStore = useHaswStore() as any

const { scene, nodes } = Resource.getItem('rooms')

const ma = new THREE.MeshPhysicalMaterial({ color: '#707070' })
ma.reflectivity = 6
ma.ior = 1.2
ma.roughness = 1
ma.metalness = 0
ma.transparent = true
ma.opacity = 0.88
// ma.transmission = 1
Object.values(nodes).forEach((node: any) => {
    node.material = ma
})
</script>
