import { defineStore } from 'pinia'
import { reactive, ref } from 'vue'

export const useHaswStore = defineStore('hasw', () => {
    const lights = reactive({
        living: true,
        bedA: true,
        bedB: true,
    })
    const tv = reactive({
        switch: true,
        live: '',
    })

    const infors = reactive({
        living: {
            temp: 16,
            hum: 30,
        },
        bedA: {
            temp: 16,
            hum: 30,
        },
        bedB: {
            temp: 16,
            hum: 30,
        },
    })

    return { lights, tv, infors }
})
